}

.content {
  padding-bottom: 3em;
}

:root {
  --text-indent: 1.5rem;
  --column-left: calc(7% + var(--text-indent));
  --column-width: calc(89% - var(--text-indent));
  hyphens: auto;
  --color-bg: #001168;
  --color-one: #1faef579;
  --color-two: #5ae4e486;
  --color-three: #ffa600;
  --color-signal: #0af;
  font-family: Work Sans, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.6;
}

@media (width >= 650px) {
  :root {
    --text-indent: 4rem;
    --column-left: calc(12% + var(--text-indent));
    --column-width: calc(85% - var(--text-indent));
    font-size: 20px;
  }
}

@media (width >= 940px) {
  :root {
    --column-left: calc(18% + var(--text-indent));
    --column-width: calc(75% - var(--text-indent));
    font-size: 21px;
  }
}

@media (width >= 1200px) {
  :root {
    --column-left: calc(30% + var(--text-indent));
    --column-width: calc(55% - var(--text-indent));
  }
}

@media (width >= 1500px) {
  :root {
    font-size: 1.4vw;
  }
}

p {
